import ExTable from './ex-table-pager';
import ElLabel from './el-label/index';
import ExCollapse from './collapse/index';
const components = {
  ExTable,
  ElLabel,
  ExCollapse
}
export default components;

export function install(Vue) {
  for (const el in components) {
    Vue.component(el, components[el])
  }
}



