import Vue from 'vue';
import Router from 'vue-router';
import InWarehousing from '../pages/in-warehousing/index';
import StoresList from '../pages/stores-list/index';
import ProjectInfo from '../pages/project-info/index';
import TabPage from '../pages/tab-page/index';
import Home from '../pages/home/index';

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Home
    },
    {
      path: '/TabPage',
      name: 'tab-page',
      component: TabPage
    },
    {
      path: '/ProjectInfo',
      name: 'ProjectInfo',
      component: ProjectInfo
    },
    {
      path: '/StoresList',
      name: 'StoresList',
      component: StoresList
    },
    {
      path: '/InWarehousing',
      name: 'InWarehousing',
      component: InWarehousing
    }
  ]
})



