<template>
  <div class="flexbox flex-vcenter" :class="inputContainerClassStyle">
    <div
      v-if="calTitle"
      class="label-wrap"
      :class="inputLabelClassStyle"
      :style="{width:width+'px'}"
    >
      <span v-if="required" style="color:red">*</span>
      {{title}}
    </div>
    <div class="flex">
      <slot/>
    </div>
  </div>
</template>
<script>
export default {
  name: "ElLabel",
  props: {
    title: {
      type: String,
      default: ""
    },
    width: {
      type: [Number, String],
      default: 80
    },
    inputContainerClassStyle: {
      type: String,
      default: ""
    },
    inputLabelClassStyle: {
      type: String,
      default: ""
    },
    required: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    calTitle() {
      return Boolean(this.title);
    }
  },
  data() {
    return {};
  },
  mounted() {},
  methods: {}
};
</script>
<style lang="css">
.label-wrap {
  color: #3f3f3f;
  padding: 0 10px;
  width: 100px;
  text-align: right;
  font-size: 14px;
}
</style> 
 


