import {
  request
} from '../../comm';
export default {
  components: {},
  data() {
    return {
      input: '',
      options: [{
        value: '选项1',
        label: '黄金糕'
      }, {
        value: '选项2',
        label: '双皮奶'
      }, {
        value: '选项3',
        label: '蚵仔煎'
      }, {
        value: '选项4',
        label: '龙须面'
      }, {
        value: '选项5',
        label: '北京烤鸭'
      }],
      value: '',
      date: '',
      data: [],
      selected: false,
      multipleSelection: []
    }
  },
  mounted() {
    this.$refs.table.fetchData();
  },
  methods: {
    handleSearch(pagination, {
      currentPage,
      pageSize
    }) {
      this.fetchRemoteData(currentPage, pageSize)
    },
    async fetchRemoteData(currentPage, pageSize) {
      //currentPage：当前页， pageSize: 每页最大条目数， 用于服务端分页
      //假设http请求数据，结果返回｛data_list: [...], total: ..｝
      //设置表格数据
      const res = await request(`/v2/movie/top250?start=${currentPage}&count=${pageSize}`);
      this.data = res.subjects;
      //设置分页总数
      const pagination = this.$refs.table.pagination;
      pagination.total = res.total;
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
      console.log(val);
    }
  }
};



