import Vue from 'vue';
import {
  Table,
  Pagination
} from 'element-ui';

export default {
  name: 'ExTable',
  mixins: [Table],
  props: {
    showPagination: {
      type: Boolean,
      default: true
    },
    pager_layout: {
      type: String,
      default: 'total,sizes,prev, pager, next, jumper'
    },
    pageSize: {
      type: Number,
      default: 20
    },
    pageSizes: {
      type: Array,
      default: () => [10, 25, 50, 100]
    },
    searchMethod: {
      type: Function,
      default: () => {}
    }
  },
  data() {
    return {
      pagination: null
    }
  },
  methods: {
    setCurrentPage(page) {
      this.pagination.currentPage = page;
      this.fetchData();
    },
    setPageSize(size) {
      this.pagination.pageSize = size;
      this.fetchData();
    },
    fetchData() {
      if (!this.showPagination) {
        throw new Error('该组件只支持分页，请检查是否正确的设置了 :show-pagination="false" 属性')
      }
      this.searchMethod(this.pagination, {
        currentPage: this.pagination.currentPage,
        pageSize: this.pagination.pageSize
      });
    },
    mountPagination() {
      const container = document.createElement('div');
      container.setAttribute('table-pager-wrap', 'table-pager-wrap');
      const parent = this.$el.parentNode;
      if (parent.lastChild == this.$el) {
        parent.appendChild(container);
      } else {
        parent.insertBefore(container, this.$el.nextSibling);
      }
      const Pager = Vue.extend(Pagination);
      this.pagination = new Pager({
        components: {
          Pagination
        },
        propsData: {
          pageSize: this.pageSize,
          pageSizes: this.pageSizes,
          layout: this.pager_layout,
          total: 0,
          currentPage: 0
        }
      });
      this.pagination.$on('current-change', this.setCurrentPage);
      this.pagination.$on('size-change', this.setPageSize);
      this.pagination.$mount(container, true);
    }
  },
  mounted() {
    if (this.showPagination) {
      this.mountPagination();
    }
  },
  beforeDestroy() {
    this.pagination.$off('current-change', this.setCurrentPage);
    this.pagination.$off('size-change', this.setPageSize);
  }
}



